        </h1>
        <div class="m-editor">
            <el-row>
                <el-col :span="12">
                    <div class="m-col m-col-left">
                        <h2 class="u-subtitle"><svg height="512" viewBox="0 0 512 512" width="512" xmlns="http://www.w3.org/2000/svg" v-bind:class="''" v-bind:svg-inline="''" v-bind:role="'presentation'" v-bind:focusable="'false'" v-bind:tabindex="'-1'"><path d="M504 275.09a8 8 0 00-5.27-7.52L384 225.85V92.23h-.31a7.922 7.922 0 00-4.96-9.66l-120-43.63a7.888 7.888 0 00-5.46 0l-120 43.63a8.018 8.018 0 00-5.06 5.78H128v137.5L13.27 267.57a8.018 8.018 0 00-5.06 5.78H8v141.82h.2a7.988 7.988 0 005.07 9.26l120 43.63a7.854 7.854 0 005.46 0L256 425.42l117.27 42.64a7.854 7.854 0 005.46 0l120-43.63a7.985 7.985 0 004.97-5.38h.3V277.23s-.006-1.416 0-2.14zM128 449.12L24 411.31v-124.8l104 37.82zm8-138.91l-96.59-35.12 95.96-34.9 96.6 35.13zm112 101.1l-104 37.81V324.33l104-37.82zm0-147.64l-104-37.82V101.51l104 37.82zM159.41 90.09L256 54.97l96.59 35.12L256 125.21zM368 101.51v124.34l-104 37.82V139.33zm0 347.61l-104-37.81v-124.8l104 37.82zm8-138.91l-95.97-34.89 96.6-35.13 95.96 34.9zm112 101.1l-104 37.81V324.33l104-37.82z"/></svg></h2>
                        <el-form class="u-cmds" ref="form" :model="form" label-width="80px">
                            <el-form-item label="">
                                <el-radio-group v-model="castType">
                                    <el-radio label="cast">(cast)</el-radio>
                                    <el-radio label="fcast">(fcast)</el-radio>
