                                            >
                                                <el-option label="" value="buff"></el-option>
                                                <el-option label="" value="buff_level"></el-option>
                                                <el-option label="" value="tbuff"></el-option>
                                                <el-option label="" value="tbuff_level"></el-option>
                                                <el-option label="" value="bufftime"></el-option>
                                                <el-option label="" value="tbufftime"></el-option>
                                                <el-option label="" value="nobuff"></el-option>
                                                <el-option label="" value="tnobuff"></el-option>
                                                <el-option label="NPC" value="npclevel"></el-option>
                                                <el-option label="" value="life"></el-option>
                                                <el-option label="" value="mana"></el-option>
                                                <el-option label="///" value="rage"></el-option>
                                                <el-option
                                                    label="//"
                                                    value="qidian"
                                                ></el-option>
                                                <el-option label="//" value="energy"></el-option>
                                                <el-option label="/" value="sun"></el-option>
                                                <el-option label="" value="moon"></el-option>
                                                <el-option label="" value="sun_power"></el-option>
                                                <el-option label="" value="moon_power"></el-option>
                                                <el-option
                                                    label=""
                                                    value="skill_energy"
                                                ></el-option>
                                                <el-option label="/ID" value="skill"></el-option>
                                                <el-option label="/ID" value="noskill"></el-option>
                                                <el-option
                                                    label=""
                                                    value="last_skill"
                                                ></el-option>
                                                <el-option label="3" value="nearby_enemy"></el-option>
                                                <el-option label="" value="skill_notin_cd"></el-option>
                                                <el-option label="" value="yaoxing"></el-option>
                                            </el-select>
                                            <el-row :gutter="2">
                                                <el-col
                                                    :span="10"
                                                    v-if="needsConditionParams.subname.includes(condition.name)"
                                                >
                                                    <el-input v-model="condition.subname" placeholder=""></el-input>
                                                </el-col>
                                                <el-col
                                                    :span="7"
                                                    v-if="needsConditionParams.relation.includes(condition.name)"
                                                >
                                                    <el-select
                                                        v-model="condition.relation"
                                                        v-if="
                                                            !needsConditionParams.relationRestricted.includes(
                                                                condition.name
                                                            )
                                                        "
                                                    >
                                                        <el-option label="=" value="="></el-option>
                                                        <el-option label="" value="<"></el-option>
                                                        <el-option label="" value=">"></el-option>
                                                        <el-option label="" value=">="></el-option>
                                                        <el-option label="" value="<="></el-option>
                                                        <el-option label="" value="~="></el-option>
                                                    </el-select>
                                                    <el-select v-model="condition.relation" v-else>
                                                        <el-option label="=" value="="></el-option>
                                                        <el-option label="" value="~="></el-option>
                                                    </el-select>
                                                </el-col>
                                                <el-col
                                                    :span="7"
                                                    v-if="needsConditionParams.value.includes(condition.name)"
                                                >
                                                    <el-input v-model="condition.value" placeholder=""></el-input>
                                                </el-col>
                                            </el-row>
                                        </el-col>
                                        <el-col :span="7">
                                            <el-button
                                                type="danger"
                                                plain
                                                circle
                                                icon="el-icon-minus"
                                                @click="clickMinusCondition(index)"
                                                v-if="conditions.length > 1"
                                            ></el-button>
                                            <el-button
                                                type="primary"
                                                plain
                                                circle
                                                icon="el-icon-plus"
                                                @click="clickPlusCondition"
                                                style="margin-left: 2px"
                                                v-if="index === conditions.length - 1"
                                            ></el-button>
                                        </el-col>
                                    </el-row>
                                </el-form-item>
                            </template>
                        </el-form>
                        <div class="u-submit">
                            <el-button type="primary" icon="el-icon-right" class="u-btn" @click="insertLine"
                                ></el-button
                            >
                        </div>
                    </div>
                </el-col>
                <el-col :span="12">
                    <div class="m-col m-col-right">
                        <h2 class="u-subtitle"><svg height="512" viewBox="0 0 512 512" width="512" xmlns="http://www.w3.org/2000/svg" v-bind:class="''" v-bind:svg-inline="''" v-bind:role="'presentation'" v-bind:focusable="'false'" v-bind:tabindex="'-1'"><linearGradient id="a" gradientUnits="userSpaceOnUse" x1="63.155" x2="450.135" y1="579.855" y2="80.38"><stop offset="0" stop-color="#003f8a"/><stop offset=".518" stop-color="#00d7df"/><stop offset="1" stop-color="#006df0"/></linearGradient><path d="M504 275.09a8 8 0 00-5.27-7.52L384 225.85V92.23h-.31a7.922 7.922 0 00-4.96-9.66l-120-43.63a7.888 7.888 0 00-5.46 0l-120 43.63a8.018 8.018 0 00-5.06 5.78H128v137.5L13.27 267.57a8.018 8.018 0 00-5.06 5.78H8v141.82h.2a7.988 7.988 0 005.07 9.26l120 43.63a7.854 7.854 0 005.46 0L256 425.42l117.27 42.64a7.854 7.854 0 005.46 0l120-43.63a7.985 7.985 0 004.97-5.38h.3V277.23s-.006-1.416 0-2.14zM128 449.12L24 411.31v-124.8l104 37.82zm8-138.91l-96.59-35.12 95.96-34.9 96.6 35.13zm112 101.1l-104 37.81V324.33l104-37.82zm0-147.64l-104-37.82V101.51l104 37.82zM159.41 90.09L256 54.97l96.59 35.12L256 125.21zM368 101.51v124.34l-104 37.82V139.33zm0 347.61l-104-37.81v-124.8l104 37.82zm8-138.91l-95.97-34.89 96.6-35.13 95.96 34.9zm112 101.1l-104 37.81V324.33l104-37.82z" fill="url(#a)"/></svg></h2>
                        <p class="u-tips">Tab</p>
                        <codemirror v-model="code" :options="cmOptions" @input="onCmCodeChange" ref="cmEditor" />
                        <div class="u-count">
                            <b :class="{ warning: code.length > 128 }">{{ code.length }}</b>
                            / 128
                            <em> {{ 128 - code.length }} </em>
                        </div>
                    </div>
                </el-col>
            </el-row>
        </div>
    </app-layout>
</template>

<script>
import { codemirror } from "vue-codemirror";
import CodeMirror from "codemirror";
// import base style
import "codemirror/lib/codemirror.css";
import "codemirror/mode/javascript/javascript.js";
import "codemirror/addon/hint/show-hint.js";
import "codemirror/addon/hint/show-hint.css";
import "codemirror/addon/edit/matchbrackets.js";
import "@/utils/z-macro.js";
import AppLayout from "@/layout/AppLayout.vue";
export default {
    name: "MacroEditor",
    data: function () {
        return {
            windowInnerWidth: window.innerWidth,
            castType: "cast",
            form: null,
            skill: "",
            isConditional: false,
            needsConditionParams: {
                subname: [
                    "buff",
                    "buff_level",
                    "nobuff",
                    "bufftime",
                    "skill_energy",
                    "skill",
                    "noskill",
                    "skill_notin_cd",
                    "tbuff",
                    "tbuff_level",
                    "tnobuff",
                    "tbufftime",
                ],
                relation: [
                    "buff_level",
                    "tbuff_level",
                    "bufftime",
                    "life",
                    "mana",
                    "rage",
                    "qidian",
                    "energy",
                    "sun",
                    "moon",
                    "skill_energy",
                    "last_skill",
                    "npclevel",
                    "nearby_enemy",
                    "tbufftime",
                    "yaoxing",
                ],
                value: [
                    "buff_level",
                    "tbuff_level",
                    "bufftime",
                    "life",
                    "mana",
                    "rage",
                    "qidian",
                    "energy",
                    "sun",
                    "moon",
                    "skill_energy",
                    "last_skill",
                    "npclevel",
                    "nearby_enemy",
                    "tbufftime",
                    "yaoxing",
                ],
                relationRestricted: ["skill", "noskill", "last_skill"],
            },
            conditions: [
                {
                    name: "",
                    relation: "=",
                    value: "",
                    logic: "&",
                    subname: "",
                },
            ],
            conditionValueName: {
                buff: "",
                buff_level: "",
                nobuff: "",
                bufftime: "",
                life: "",
                mana: "",
                rage: "///",
                qidian: "",
                energy: "//",
                sun: "/",
                moon: "",
                sun_power: "",
                moon_power: "",
                skill_energy: "",
                skill: "/ID",
                noskill: "/ID",
                last_skill: "",
                npclevel: "NPC",
                nearby_enemy: "3",
                skill_notin_cd: "",
                tbuff: "",
                tbuff_level: "",
                tnobuff: "",
                tbufftime: "",
            },
            logicDialogVisible: false,
            logicDialog: "",
            region: "",
            cmOptions: {
                tabSize: 4,
                lineNumbers: true,
                line: true,
                styleActiveLine: true,
                lineNumbers: true,
                mode: "text/x-macro",
                extraKeys: { Tab: "autocomplete" },
                matchBrackets: true,
                hintOptions: {
                    completeSingle: false,
                },
                // more CodeMirror options...
            },
            code: "",
        };
    },
    computed: {
        codemirror() {
            return this.$refs.cmEditor.codemirror;
        },
    },
    methods: {
        clickPlusCondition() {
            this.conditions.push({
                name: "",
                relation: "=",
                value: "",
                logic: "&",
                subname: "",
            });
        },
        clickMinusCondition(index) {
            this.conditions.splice(index, 1);
        },
        onConditionChange(index) {
            let thisName = this.conditions[index].name;
            let thisRelation = this.conditions[index].relation;
            if (thisName === "skill" || thisName === "noskill" || thisName === "last_skill") {
                if (thisRelation !== "=" && thisRelation !== "") {
                    // // 
                    // let tmpArr = this.conditions
                    // tmpArr[index].relation = '='
                    // this.conditions = tmpArr
                    this.conditions[index].relation = "=";
                }
            }
        },
        insertLine() {
            let line = "/" + this.castType + " ";
            if (this.isConditional) {
                let allConditions = "[";
                for (let i = 0; i < this.conditions.length; ++i) {
                    let condition = this.conditions[i];
                    if (i !== 0) {
                        allConditions += condition.logic;
                    }
                    allConditions += condition.name;
                    if (this.needsConditionParams.subname.includes(condition.name)) {
                        allConditions = allConditions + ":" + condition.subname;
                    }
                    if (this.needsConditionParams.relation.includes(condition.name)) {
                        allConditions = allConditions + condition.relation;
                    }
                    if (this.needsConditionParams.value.includes(condition.name)) {
                        allConditions = allConditions + condition.value;
                    }
                }
                allConditions += "]";
                line = line + allConditions + " ";
            }
            line = line + this.skill + "\n";
            this.code += line.replace(/buff_level/g, "buff");
        },
        onCmCodeChange(value) {
            // this.codemirror.showHint();
        },
        showRealLogic() {
            if (this.conditions <= 2) {
                return false;
            } else {
                let allConditions = "";
                for (let i = 0; i < this.conditions.length; ++i) {
                    let condition = this.conditions[i];
                    if (i === this.conditions.length - 1) {
                        allConditions +=
                            // condition.logic === "&" ? "  " : "  ";
                            condition.logic === "&" ? " & " : " | ";
                    } else if (i !== 0) {
                        allConditions +=
                            // condition.logic === "&" ? " " : " ";
                            condition.logic === "&" ? " &" : " |";
                    }
                    // allConditions += this.conditionValueName[condition.name];
                    allConditions += condition.name;
                    if (this.needsConditionParams.subname.includes(condition.name)) {
                        allConditions = allConditions + ":" + condition.subname;
                    }
                    if (this.needsConditionParams.relation.includes(condition.name)) {
                        allConditions = allConditions + condition.relation;
                    }
                    if (this.needsConditionParams.value.includes(condition.name)) {
                        allConditions = allConditions + condition.value;
                    }
                }
                for (let parenthesis = 0; parenthesis < this.conditions.length - 2; ++parenthesis) {
                    allConditions += "";
                }
                this.logicDialog = allConditions.replace(/buff_level/g, "buff");
                this.logicDialogVisible = true;
            }
        },
        getUserId() {
            if (User.isLogin()) {
                this.uid = User.getInfo().uid;
            }
        },
    },
    components: {
        codemirror,
        AppLayout,
    },
};
</script>

<style lang="less">
